global.Parser = {};
import trees from './trees'
import myAudio from './audio'
export default {
	components: {
		myAudio,
		trees
	},
	name: 'trees',
	data() {
		return {
			controls: [],
			placeholder: 'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="300" height="225"/>',
			loadVideo:




			true

		}
	},
	props: {
		nodes: Array,
		lazyLoad: Boolean
	},
	mounted() {
		// 获取顶层组件
		var _top = this.$parent;
		while (_top.$options.name != 'parser') {
			if (_top.top) {
				_top = _top.top;
				break;
			}
			_top = _top.$parent;
		}
		this.top = _top;
		for (var j = this.nodes.length, item; item = this.nodes[--j];) {
			if (item.c) continue;
			if (item.name == 'img')
				_top.imgList.setItem(item.attrs.i, item.attrs.src);
			else if (item.name == 'video' || item.name == 'audio') {
				var ctx;
				if (item.name == 'video')
					ctx = uni.createVideoContext(item.attrs.id

						, this

					);
				else if (this.$refs[item.attrs.id])
					ctx = this.$refs[item.attrs.id][0];
				if (ctx) {
					ctx.id = item.attrs.id;
					console.log(item);
					_top.videoContexts.push(ctx);
				}
			}









		} 






	},
	methods: {
		play(e) {
			var contexts = this.top.videoContexts;
			if (contexts.length > 1 && this.top.autopause)
				for (var i = contexts.length; i--;)
					if (contexts[i].id != e.currentTarget.dataset.id)
						contexts[i].pause();
		},
		imgtap(e) {
			var attrs = e.currentTarget.dataset.attrs;
			if (!attrs.ignore) {
				var preview = true,
					data = {
						id: e.target.id,
						src: attrs.src,
						ignore: () => preview = false
					};
				global.Parser.onImgtap && global.Parser.onImgtap(data);
				this.top.$emit('imgtap', data);
				if (preview) {
					var urls = this.top.imgList,
						current = urls[attrs.i] ? parseInt(attrs.i) : (urls = [attrs.src], 0);
					uni.previewImage({
						current,
						urls
					})
				}
			}
		},
		loadImg(e) {
			var i = e.currentTarget.dataset.i
			if (this.lazyLoad && !this.controls[i])
				this.$set(this.controls, i, {});
		},
		linkpress(e) {
			var jump = true,
				attrs = e.currentTarget.dataset.attrs;
			attrs.ignore = () => jump = false;
			global.Parser.onLinkpress && global.Parser.onLinkpress(attrs);
			this.top.$emit('linkpress', attrs);
			if (jump) {

				if (attrs['app-id']) {
					return uni.navigateToMiniProgram({
						appId: attrs['app-id'],
						path: attrs.path
					})
				}

				if (attrs.href) {
					if (attrs.href[0] == '#') {
						if (this.top.useAnchor)
							this.top.navigateTo({
								id: attrs.href.substring(1)
							})
					} else if (attrs.href.indexOf('http') == 0 || attrs.href.indexOf('//') == 0) {




						uni.setClipboardData({
							data: attrs.href,
							success: () =>
								uni.showToast({
									title: '链接已复制'
								})
						})

					} else
						uni.navigateTo({
							url: attrs.href,
							fail() {
								uni.switchTab({
									url: attrs.href,
								})
							}
						})
				}
			}
		},
		error(e) {
			var context, src = '',
				target = e.currentTarget,
				source = target.dataset.source,
				i = target.dataset.i;
			if (source == 'video' || source == 'audio') {
				// 加载其他 source
				var index = this.controls[i] ? this.controls[i].i + 1 : 1;
				if (index < this.nodes[i].attrs.source.length) {
					if (this.controls[i])
						this.controls[i].i = index;
					else
						this.$set(this.controls, i, {
							i: index
						});
				}
				if (source == 'video') context = uni.createVideoContext(target.id, this);
				else if (e.detail.__args__) {
					e.detail = e.detail.__args__[0];
					context = e.detail.context;
				}
			} else if (source == 'img')
				context = {
					setSrc: src => {
						this.top.imgList.setItem(target.dataset.index, src);
						this.$set(this.controls[i], 'src', src);
					}
				}
			var obj = {
				source,
				target,
				errMsg: e.detail.errMsg,
				errCode: e.detail.errCode,
				context
			}
			global.Parser.onError && global.Parser.onError(obj);
			this.top && this.top.$emit('error', obj);
		},
		_loadVideo(e) {
			this.$set(this.controls, e.target.dataset.i, {
				play: true
			});
		}
	}
}
